import { Component, OnInit, Inject, Optional, Injector } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { AssetService } from '../../../../api/services/asset.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-assets-maintenance-history',
  templateUrl: './assets-maintenance-history.component.html',
  styleUrls: ['./assets-maintenance-history.component.scss']
})
export class AssetsMaintenanceHistoryComponent implements OnInit {
  
 
  isAssetMaintenanceLoaded:boolean = false;
  maintenanceDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 5;

  statusTypeData:any;

  constructor(
  	@Inject(MAT_DIALOG_DATA) public assetId: any,
  	private assetService: AssetService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }


  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }


  getDateFormat(date){
    return moment(date).format("MM-DD-YYYY");
  }

  getMaintenanceStatus(id){
    
    var data = _.filter( this.statusTypeData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase().replace(/-/g, ' ');
    }

  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  ngOnInit() {

  	this.assetService.getAllAssetMaintenancePlanByAssetId(this.assetId).subscribe((res:any) => {

  		this.isAssetMaintenanceLoaded = true;
  		this.maintenanceDataList = res;

  		if(this.totalItems>this.itemLimit){
  			this.ItemEndIndex = this.itemLimit;
  		}
  		else {
  			this.ItemEndIndex = this.totalItems;
  		}

  	 	this.totalItems = this.maintenanceDataList.length;

  	});

    this.lookupService.getLookupValueByLookupTypeId(64).subscribe((res:any) => {
      this.statusTypeData = res;
    },
    error => {

    });

  }

}
