import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { AssetsComponent } from './assets.component';
import { AssetsSetupComponent } from './components/assets-setup/assets-setup.component';
import { AssetsCreateComponent } from './components/assets-create/assets-create.component';
import { AssetsReportsComponent } from './components/assets-reports/assets-reports.component';
import { AssetsUpdateComponent } from './components/assets-update/assets-update.component';
import { AssetsViewComponent } from './components/assets-view/assets-view.component';
import { AddAssetMaintenanceComponent } from './components/add-asset-maintenance/add-asset-maintenance.component';
import { AssetsMaintenanceHistoryComponent } from './components/assets-maintenance-history/assets-maintenance-history.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: AssetsSetupComponent},
	{ path: 'create-asset', component: AssetsCreateComponent},
	{ path: 'edit-asset/:id', component: AssetsCreateComponent},
	{ path: 'add-asset-maintenance/:id/:type', component: AddAssetMaintenanceComponent},
	{ path: 'edit-asset-maintenance/:id', component: AddAssetMaintenanceComponent},
	{ path: 'view', component: AssetsViewComponent},
	{ path: 'view/:id', component: AssetsViewComponent},
	{ path: 'reports', component: AssetsReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AssetsRoutingModule { }
